@import url(https://fonts.googleapis.com/css2?family=Open+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{padding:0;margin:0;box-sizing:border-box;font-family:'Open Sans', sans-serif}@media (min-width: 360px) and (max-width: 768px){.App{width:100%;margin:0 auto}}@media (min-width: 768px){.App{width:768px;margin:0 auto}}@media (min-width: 1024px){.App{width:1024px;margin:0 auto}}@media (min-width: 2560px){.App{width:1170px;margin:0 auto}}

.body{padding-top:36px}

.firstsection{background-image:url(/react-deploy/static/media/banner-photo.9614ebc2.jpg);background-size:cover;background-position:50%;width:100%;display:flex;flex-direction:column;align-items:center;padding-top:76px}.firstsection__title{white-space:pre-wrap;text-transform:uppercase;font-size:30px;text-align:center;color:#ffffff;line-height:1.13;font-weight:normal}.firstsection__description{margin-top:29px;width:327px;font-size:16px;line-height:1.5;text-align:center;color:#ffffff;font-weight:normal}.firstsection__button{margin-top:26px;margin-bottom:75px;width:210px;height:38px;border-radius:4px;background-color:#ef5b4c;color:white;font-size:16px;text-align:center;padding:7px;text-decoration:none}.wrap{width:327px}

.section2{display:flex;flex-direction:column;align-items:center;width:330px;margin:0 auto}.section2__title{margin-top:80px;font-size:30px;font-family:'Open Sans', sans-serif;margin-bottom:32px;font-weight:normal;text-align:center;color:#212529;letter-spacing:normal;line-height:50px}.section2__img{margin:0 auto;width:289px;height:243px}.section2__topic{margin:0 auto;margin-top:40px;margin-bottom:24px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:1.25;letter-spacing:normal;text-align:center;color:#212529}.section2__paragraph{font-size:16px;font-weight:normal;font-stretch:normal;font-style:normal;line-height:1.5;letter-spacing:normal;text-align:justify;color:#4c4b4b;margin-bottom:36px}.section2__signup{height:17px;font-size:16px;text-align:center;font-weight:600;color:#ef5b4c;margin-bottom:76px;text-decoration:none}.section2__signup:hover{color:#cb3c32}

.users{display:flex;flex-direction:column;align-items:center;background-color:#f9f9f3}.users__title{margin-top:80px;margin-bottom:74px}.users__button{margin-bottom:80px;width:210px;height:38px;border-radius:4px;background-color:#ef5b4c;border:none;color:white;font-size:16px;font-weight:normal;outline:none}.users__batch{height:850px;overflow:hidden}@media (min-width: 768px){.users{height:1105px}.users__batch{height:580px;overflow:unset;margin-bottom:40px;display:grid;grid-template-columns:repeat(3, 1fr);grid-template-rows:repeat(2, 1fr);grid-column-gap:0px;grid-row-gap:0px}}

.formwrapper{display:flex;flex-direction:column;align-items:center;margin-top:80px}.formwrapper__title{font-size:30px;text-align:center;color:#212529;margin-bottom:118px}.form{margin:0 auto;width:330px}.form__fieldphoto{display:flex;flex-direction:row}.form__intputfield{padding:5px}.button{display:block;margin:0 auto;margin-top:40px;width:210px;height:38px;border-radius:4px;background-color:#ef5b4c;color:#fff;outline:none;border:none}.photo-input{display:flex;flex-direction:row}

.logo{margin:11px 0 0 15px;width:20%}@media (max-width: 1024px){.logo{margin:20px 0 0 27px}}

.bm-burger-button{position:fixed;width:20px;height:17px;right:15px;top:13px}@media (min-width: 769px){.bm-burger-button{display:none}.bm-menu-wrap{display:none}.bm-menu{display:none}.bm-overlay{display:none}.bm-morph-shape{display:none}.burgerwrapp{display:none}}.bm-burger-bars{background:#1b4a8a}.bm-burger-bars-hover{background:#a90000}.bm-menu-wrap{margin-left:-100%;position:fixed;height:100%}.bm-menu{background:#ffffff;font-size:16px;line-height:24px}.bm-morph-shape{fill:#373a47}.bm-item-list{padding:0.8em}.bm-overlay{background:rgba(0,0,0,0.3)}.bm-item{display:inline-block;color:#212529;margin-bottom:10px;text-align:left;text-decoration:none;transition:color 0.2s;outline:none}.bm-item:hover{color:#ef5b4c}.bm-item:active{color:#007bff}

.menu{width:45%;display:flex;flex-direction:row;justify-content:space-around;align-items:center;margin-right:30px}.menu__item{font-size:14px;display:block;color:#212529;text-align:center;text-decoration:none;transition:color 0.2s;outline:none;font-weight:normal;font-stretch:normal;font-style:normal;line-height:4.29;letter-spacing:normal;color:#212529}.menu__item:hover{color:#ef5b4c}.menu__item:active{color:#007bff}@media (max-width: 769px){.menu{display:none}}

.header{position:fixed;width:100%;display:flex;flex-direction:row;justify-content:space-between;background-color:#fff;z-index:4}@media (max-width: 1024px){.header{height:60px}}

.footer{margin-top:80px;width:100%;border-top:solid 1px #ececec;height:60px}.footer__text{margin:21px auto;font-size:16px;line-height:1.5;letter-spacing:normal;text-align:center;color:#4c4b4b}

